<template>
    <div class="article-detail">
        <div v-if="loading" class="loading">
            <div class="loading-spinner"></div>
            <p>Âä†ËΩΩ‰∏≠...</p>
        </div>

        <div v-else-if="error" class="error">
            <p>{{ error }}</p>
            <router-link to="/articles" class="back-link">ËøîÂõûÊñáÁ´†ÂàóË°®</router-link>
        </div>

        <div v-else class="article-content">
            <h1>{{ article.title }}</h1>
            <div class="article-meta">
                <span class="article-date">{{ article.date }}</span>
                <div class="article-tags">
                    <span v-for="(tag, index) in article.tags" :key="index" class="tag">{{ tag }}</span>
                </div>
            </div>

            <div class="article-body" v-html="article.content"></div>

            <div class="article-actions">
                <router-link to="/articles" class="back-link">ËøîÂõûÊñáÁ´†ÂàóË°®</router-link>
            </div>
        </div>
    </div>
</template>

<script>
import api from '../api';

export default {
    name: 'ArticleDetail',
    props: {
        id: {
            type: [String, Number],
            required: true
        }
    },
    data() {
        return {
            article: {},
            loading: true,
            error: null
        }
    },
    created() {
        this.fetchArticle();
    },
    methods: {
        async fetchArticle() {
            try {
                this.loading = true;
                const response = await api.get(`/api/articles/${this.id}`);
                this.article = response.data;
                this.loading = false;

                // ËÆæÁΩÆÈ°µÈù¢Ê†áÈ¢ò
                document.title = `${this.article.title} - ‰∏™‰∫∫ÁΩëÁ´ô`;
            } catch (err) {
                console.error('Ëé∑ÂèñÊñáÁ´†ËØ¶ÊÉÖÂ§±Ë¥•:', err);
                this.error = 'Ëé∑ÂèñÊñáÁ´†ËØ¶ÊÉÖÂ§±Ë¥•ÔºåËØ∑Á®çÂêéÂÜçËØï';
                this.loading = false;
            }
        }
    }
}
</script>

