<template>
  <div class="tools">
    <h1>Â∑•ÂÖ∑ÁÆ±</h1>

    <div v-if="error" class="error">
      {{ error }}
    </div>
    <div v-else class="tools-container">
      <!-- Â∑¶‰æßÂàÜÁ±ªÂàóË°® -->
      <div class="categories-sidebar">
        <h3>ÂàÜÁ±ª</h3>
        <ul class="category-list">
          <template v-if="categories.length > 0">
            <li v-for="category in categories" :key="category" :class="{ active: selectedCategory === category }"
              @click="selectCategory(category)">
              {{ category }}
            </li>
          </template>
          <template v-else>
            <li v-for="i in 4" :key="i" class="placeholder category-placeholder"></li>
          </template>
        </ul>
      </div>

      <!-- Âè≥‰æß‰∏ª‰ΩìÂÜÖÂÆπ -->
      <div class="tools-content">
        <template v-if="tools.length > 0">
          <!-- ÊåâÂàÜÁ±ªÂàÜÁªÑÊòæÁ§∫ -->
          <template v-if="selectedCategory === 'ÂÖ®ÈÉ®'">
            <div v-for="category in categories.filter(c => c !== 'ÂÖ®ÈÉ®')" :key="category" class="category-section">
              <h2 class="category-title">{{ category }}</h2>
              <div class="tools-grid">
                <a v-for="tool in getToolsByCategory(category)" :key="tool.id" :href="tool.url" target="_blank"
                  class="tool-card">
                  <h3>{{ tool.name }}</h3>
                  <p>{{ tool.description }}</p>
                </a>
              </div>
            </div>
          </template>

          <!-- ÊòæÁ§∫Âçï‰∏™ÂàÜÁ±ª‰∏ãÁöÑÂ∑•ÂÖ∑ -->
          <template v-else>
            <h2 class="category-title">{{ getCurrentCategoryName() }}</h2>
            <div class="tools-grid">
              <a v-for="tool in filteredTools" :key="tool.id" :href="tool.url" target="_blank" class="tool-card">
                <h3>{{ tool.name }}</h3>
                <p>{{ tool.description }}</p>
              </a>
            </div>
          </template>
        </template>

        <!-- Âç†‰ΩçÁ¨¶ -->
        <template v-else>
          <h2 class="category-title placeholder title-placeholder" style="width: 200px; margin-bottom: 30px;"></h2>
          <div class="tools-grid">
            <div v-for="i in 6" :key="i" class="tool-card placeholder-card">
              <div class="placeholder title-placeholder"></div>
              <div class="placeholder description-placeholder"></div>
              <div class="placeholder description-placeholder" style="width: 85%"></div>
            </div>
          </div>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
import api from '../api';

export default {
  name: 'Tools',
  data() {
    return {
      tools: [],
      categories: [],
      selectedCategory: 'ÂÖ®ÈÉ®', // ÈªòËÆ§ÈÄâÊã©"ÂÖ®ÈÉ®"
      loading: true,
      error: null
    }
  },
  computed: {
    filteredTools() {
      if (this.selectedCategory === 'ÂÖ®ÈÉ®') {
        return this.tools;
      }
      return this.tools.filter(tool => tool.category === this.selectedCategory);
    }
  },
  created() {
    this.fetchData();
  },
  methods: {
    async fetchData() {
      try {
        this.loading = true;

        // Ëé∑ÂèñÂ∑•ÂÖ∑Êï∞ÊçÆ
        const response = await api.get('/api/tools');

        // Â§ÑÁêÜAPIËøîÂõûÁöÑÊï∞ÊçÆ
        if (response.data) {
          this.tools = response.data.tools || [];
          this.categories = response.data.categories || [];

          // Á°Æ‰øù"ÂÖ®ÈÉ®"ÈÄâÈ°πÂ≠òÂú®
          if (!this.categories.includes('ÂÖ®ÈÉ®')) {
            this.categories.unshift('ÂÖ®ÈÉ®');
          }
        }

        this.loading = false;
      } catch (err) {
        console.error('Ëé∑ÂèñÂ∑•ÂÖ∑Êï∞ÊçÆÂ§±Ë¥•:', err);
        this.error = 'Ëé∑ÂèñÂ∑•ÂÖ∑Êï∞ÊçÆÂ§±Ë¥•ÔºåËØ∑Á®çÂêéÂÜçËØï';
        this.loading = false;
      }
    },
    selectCategory(category) {
      this.selectedCategory = category;
    },
    // Ëé∑ÂèñÁâπÂÆöÂàÜÁ±ª‰∏ãÁöÑÂ∑•ÂÖ∑
    getToolsByCategory(category) {
      return this.tools.filter(tool => tool.category === category);
    },
    // Ëé∑ÂèñÂΩìÂâçÈÄâ‰∏≠ÂàÜÁ±ªÁöÑÂêçÁß∞
    getCurrentCategoryName() {
      return this.selectedCategory;
    }
  }
}
</script>

<style scoped>
.tools {
  max-width: 1400px;
  margin: 0 auto;
  padding: 40px 20px;
}

h1 {
  font-weight: 300;
  margin-bottom: 40px;
  color: #333;
  text-align: center;
}

/* Â∑¶Âè≥‰∏§Ê†èÂ∏ÉÂ±Ä */
.tools-container {
  display: flex;
  gap: 20px;
}

/* Â∑¶‰æßÂàÜÁ±ªÂàóË°® */
.categories-sidebar {
  width: 180px;
  flex-shrink: 0;
}

.categories-sidebar h3 {
  font-weight: 400;
  color: #333;
  margin-top: 0;
  margin-bottom: 20px;
  padding-bottom: 10px;
  border-bottom: 1px solid #eaeaea;
}

.category-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.category-list li {
  padding: 12px 15px;
  margin-bottom: 5px;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s ease;
  color: #555;
  font-weight: 300;
}

.category-list li:hover {
  background-color: #f5f5f5;
  transform: translateX(5px);
}

.category-list li.active {
  background-color: #333;
  color: white;
  font-weight: 400;
}

.category-placeholder {
  height: 40px;
  margin-bottom: 10px;
  border-radius: 8px;
}

/* Âè≥‰æß‰∏ª‰ΩìÂÜÖÂÆπ */
.tools-content {
  flex-grow: 1;
}

.category-section {
  margin-bottom: 50px;
}

.category-title {
  font-weight: 300;
  color: #333;
  margin-top: 0;
  margin-bottom: 25px;
  padding-bottom: 10px;
  border-bottom: 1px solid #eaeaea;
}

/* Â∑•ÂÖ∑Âç°ÁâáÁΩëÊ†º */
.tools-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  margin-bottom: 40px;
}

.tool-card {
  background-color: white;
  border-radius: 12px;
  padding: 15px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.05);
  display: flex;
  flex-direction: column;
  height: auto;
  min-height: 90px;
  text-decoration: none;
  color: inherit;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  border: 1px solid rgba(0, 0, 0, 0.03);
}

.tool-card::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 3px;
  background: linear-gradient(90deg, #333 0%, #666 100%);
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 0.3s ease;
}

.tool-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.1);
}

.tool-card:hover::after {
  transform: scaleX(1);
}

.tool-card h3 {
  margin: 0 0 8px 0;
  color: #333;
  font-weight: 500;
  font-size: 1.1em;
}

.tool-card p {
  color: #666;
  line-height: 1.4;
  font-weight: 300;
  margin-bottom: 0;
  font-size: 0.9em;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}

.error {
  text-align: center;
  padding: 40px;
  color: #d32f2f;
  font-weight: 300;
}

/* Âç†‰ΩçÂç°ÁâáÊ†∑Âºè */
.placeholder-card {
  cursor: default;
  background-color: white;
  border-radius: 12px;
  padding: 15px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.05);
  display: flex;
  flex-direction: column;
  height: auto;
  min-height: 90px;
  border: 1px solid rgba(0, 0, 0, 0.03);
}

.placeholder {
  background: linear-gradient(90deg, #f7f7f7 25%, #f0f0f0 50%, #f7f7f7 75%);
  background-size: 200% 100%;
  animation: loading 1.5s infinite;
  border-radius: 4px;
}

.title-placeholder {
  height: 20px;
  width: 70%;
  margin-bottom: 8px;
}

.description-placeholder {
  height: 12px;
  width: 100%;
  margin-bottom: 6px;
}

/* ÂìçÂ∫îÂºèËÆæËÆ° */
@media (max-width: 1200px) {
  .tools-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 768px) {
  .tools-container {
    flex-direction: column;
  }

  .categories-sidebar {
    width: 100%;
    margin-bottom: 20px;
  }

  .category-list {
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
  }

  .category-list li {
    padding: 8px 16px;
    margin-bottom: 0;
    font-size: 0.9em;
  }

  .tools-grid {
    grid-template-columns: 1fr;
  }
}

@keyframes loading {
  0% {
    background-position: 200% 0;
  }

  100% {
    background-position: -200% 0;
  }
}
</style>