import axios from 'axios';
import articlesData from './data/articles.json';
import friendsData from './data/friends.json';
import toolsData from './data/tools.json';

// ÂàõÂª∫axiosÂÆû‰æãÔºà‰øùÁïô‰ª•‰æøÂ∞ÜÊù•ÂèØËÉΩÁöÑËøúÁ®ãAPIË∞ÉÁî®Ôºâ
const api = axios.create({
    baseURL: process.env.VUE_APP_API_URL || 'http://localhost:5000', // ÈªòËÆ§‰ΩøÁî®Êú¨Âú∞ÂºÄÂèëÊúçÂä°Âô®
    timeout: 5000, // ËØ∑Ê±ÇË∂ÖÊó∂Êó∂Èó¥
    headers: {
        'Content-Type': 'application/json',
    },
});

// ËØ∑Ê±ÇÊã¶Êà™Âô®
api.interceptors.request.use(
    (config) => {
        // Âú®ËøôÈáåÂèØ‰ª•Ê∑ªÂä†ËÆ§ËØÅtokenÁ≠â
        return config;
    },
    (error) => {
        return Promise.reject(error);
    }
);

// ÂìçÂ∫îÊã¶Êà™Âô®
api.interceptors.response.use(
    (response) => {
        return response;
    },
    (error) => {
        // Áªü‰∏ÄÂ§ÑÁêÜÈîôËØØ
        console.error('APIËØ∑Ê±ÇÈîôËØØ:', error);
        return Promise.reject(error);
    }
);

// ‰ªéÊú¨Âú∞JSONÊñá‰ª∂Ëé∑ÂèñÊï∞ÊçÆÁöÑÊñπÊ≥ï
api.getLocalData = (path) => {
    return new Promise((resolve, reject) => {
        let data;

        // Ê£ÄÊü•ÊòØÂê¶ÊòØËé∑ÂèñÂçïÁØáÊñáÁ´†ÁöÑËØ∑Ê±Ç
        const articleMatch = path.match(/^\/api\/articles\/(\d+)$/);
        if (articleMatch) {
            const articleId = parseInt(articleMatch[1]);
            const article = articlesData.find(a => a.id === articleId);
            if (article) {
                data = article;
            } else {
                reject(new Error('Article not found'));
                return;
            }
        } else {
            // Ê†πÊçÆË∑ØÂæÑËøîÂõûÁõ∏Â∫îÁöÑÊï∞ÊçÆ
            switch (path) {
                case '/api/articles':
                    data = articlesData;
                    break;
                case '/api/friends':
                    data = friendsData;
                    break;
                case '/api/tools':
                    data = toolsData;
                    break;
                default:
                    data = [];
            }
        }

        // Ê®°ÊãüaxiosÂìçÂ∫îÊ†ºÂºè
        resolve({
            data: data,
            status: 200,
            statusText: 'OK',
            headers: {},
            config: {}
        });
    });
};

// Ë¶ÜÁõñÂéüÊúâÁöÑgetÊñπÊ≥ïÔºå‰ºòÂÖà‰ΩøÁî®Êú¨Âú∞Êï∞ÊçÆ
const originalGet = api.get;
api.get = function (url, config) {
    // Ê£ÄÊü•ÊòØÂê¶ÊòØÊàë‰ª¨Ë¶ÅÊã¶Êà™ÁöÑAPIË∑ØÂæÑ
    if (url.startsWith('/api/articles/') || ['/api/articles', '/api/friends', '/api/tools'].includes(url)) {
        return this.getLocalData(url);
    }
    // ÂÖ∂‰ªñAPIË∞ÉÁî®‰ΩøÁî®ÂéüÂßãaxiosÊñπÊ≥ï
    return originalGet(url, config);
};

// Ê∑ªÂä†ÈùôÊÄÅËµÑÊ∫êÂü∫Á°ÄURL
api.staticBaseUrl = '';

export default api;